import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { IonSplitPane, MenuController } from '@ionic/angular';
import { ApicallService } from 'src/app/services/CallApi/apicall.service';
import { GlobalService } from 'src/app/services/data/global.service';
@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {

  @ViewChild('ionSplitPane') ionSplitPane!: IonSplitPane;
  public user : any = {username:'', password:'', role:''}

  constructor(public route : Router,  public menuCtrl: MenuController, public global : GlobalService, public apicall : ApicallService) { this.menuCtrl.enable(false); }

  ngOnInit() {
    // this.disableIonSplitPange();
      this.menuCtrl.enable(false);

  }

 async submitLogin(){
   await this.apicall.api_login(this.user);
   this.user = {username:'', password:'', role:''}
  }


  disableIonSplitPange(){
    this.ionSplitPane.disabled = true;
  }
  user_login(){
    this.route.navigate(['home']);
    console.log(this.user)
  }

}
